import {React, useState} from "react";
import "./App.css";
import {Canvas} from 'react-three-fiber';
import {OrbitControls, useGLTFLoader} from 'drei';
import Background from './space3.jpg';
import Spaceman from './spaceman.gltf';
import SpacemanButt from './spacemanbutt.gltf';

import {useSpring, a} from 'react-spring/three';




  const Butt = () => {
  const butt = useGLTFLoader(SpacemanButt, true);
  const [expand,setExpand] = useState(false);
  const props =useSpring({
    scale: expand ? [1.4, 1.4, 1.4] : [1,1,1] 
  });
  return (
    <a.primitive 
    object ={butt.scene} 
    dispose={null} 
    position={[0,3,0]} 
    scale={props.scale}
    onClick ={() => {setExpand(!expand);}}
    />
  );
}
 

function App() {
  const gltf = useGLTFLoader(Spaceman, true);

  const butt = useGLTFLoader(SpacemanButt, true);
  const [expand,setExpand] = useState(false);
  const props =useSpring({
    scale: expand ? [1.2, 1.2, 1.2] : [1,1,1]});
 
  return (
    <div className="App">
        <div className="canvas" style={{backgroundImage:`url(${Background})`}}>
          <Canvas 
          colorManagement
          camera={{position: [0,0,6], fov: 80}}>
            <ambientLight intensity={1}/>  
            {/* <directionalLight intensity ={1} position={[0,10,10]}/> */}
            <pointLight intensity={1} position={[0,10,10]}/>
            <pointLight intensity={1} position={[0,10,-10]}/>
            <pointLight intensity={1} position={[10,10,-10]}/>
            <pointLight intensity={1} position={[-10,10,-10]}/>
          
            <primitive object ={gltf.scene} dispose={null} position={[0,3,0]}/>
            <a.primitive 
              object ={butt.scene} 
              dispose={null} 
              position={[0,3,0]} 
              scale={props.scale}
              onClick ={() => {setExpand(!expand);}}
            />
            <OrbitControls autoRotate={true} autoRotateSpeed={4} enableZoom={false} />  
          </Canvas>
        </div>
    </div>
  );
}

export default App;
